.worksheet {
  padding: 15px 20px 31px 20px;

  font-family: "Oswald", "Arial", sans-serif;

  @media (min-width: 768px) {
    padding: 15px 0 52px 0;
  }

  @media (min-width: 1300px) {
    padding: 15px 0 85px 0;
  }
}

.worksheet__title {
  margin: 0 0 25px 0;

  font-size: 36px;
  line-height: 60px;
  font-weight: 400;
  color: #111111;

  @media (min-width: 768px) {
    width: 708px;
    margin: 0 auto;
    padding: 18px 0 22px 0;

    font-size: 60px;
  }

  @media (min-width: 1300px) {
    width: 1220px;
    margin: 0 auto;
    padding: 0 0 24px 0;
  }
}

.worksheet__description {
  margin: 0 0 30px 0;
  width: 230px;

  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;

  @media (min-width: 768px) {
    margin: 0 0 0 40px;
    padding: 32px 0 30px 0;
    width: 340px;

    font-size: 20px;
    line-height: 30px;
  }

  @media (min-width: 1300px) {
    margin: 0;
  }
}

.worksheet__box {
  padding: 0 20px;

  @media (min-width: 768px) {
    padding: 0 35px 0 39px;
  }

  @media (min-width: 1300px) {
    padding: 0;
  }
}

.worksheet__box--master {
  @media (min-width: 1300px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0;
  }
}

.worksheet__box-title {
  display: inline-block;
  vertical-align: middle;
  margin: -17px 0 34px 0;
  padding: 0 20px 0 19px;

  font-size: 24px;
  line-height: 1;
  font-weight: 400;
  color: #111111;

  background: #ffffff;

  @media (min-width: 768px) {
    margin: -25px 0 77px 0;
    padding: 0 40px;

    font-size: 36px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    margin: -25px 0 76px -40px;
  }
}

.worksheet__box-title--master {
  width: 220px;

  @media (min-width: 768px) {
    width: auto;
  }

  @media (min-width: 1300px) {
    margin: -25px 0 76px -40px;
  }
}

.worksheet__label {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 14px 0;

  @media (min-width: 768px) {
    margin: 0 0 21px 0;
  }

  @media (min-width: 1300px) {
    width: 570px;
  }
}

.worksheet__label-name {
  padding: 13px 0 10px 0;
  margin: 0 13px 0 0;

  font-size: 16px;
  line-height: 18px;
  text-transform: uppercase;
  color: #222222;

  @media (min-width: 768px) {
    padding: 15px 0 10px 0;
    margin: 0 17px 0 0;

    font-size: 20px;
    line-height: 1;
  }
}

.worksheet__icon {
  position: absolute;
}

.worksheet__icon--mail {
  right: 15px;
  top: 16px;

  @media (min-width: 768px) {
    right: 20px;
    top: 19px;
  }
}

.worksheet__icon--phone {
  right: 12px;
  top: 14px;

  @media (min-width: 768px) {
    right: 18px;
    top: 17px;
  }
}

.worksheet__field {
  flex-grow: 2;
  height: 45px;
  border: 1px solid #e6e6e6;
  padding: 10px 16px;


  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;

  background: none;

  &:focus {
    outline-color: #000000;
  }

  &:focus + .worksheet__icon {
    fill: @colorThemeGreen;
  }

  &:hover {
    border-color: #cdcdcd;
  }

  &:invalid {
    border-color: @colorThemeError;

    & + .worksheet__icon {
      fill: @colorThemeError;
    }
  }

  @media (min-width: 768px) {
    padding: 10px 20px;
    height: 51px;

    font-size: 20px;
    line-height: 1;
  }
}

.worksheet__field--master {
  padding: 10px 42px 10px 12px;

  @media (min-width: 768px) {
    padding: 10px 56px 10px 20px;
  }
}

.worksheet__cat-info {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin: 0 -20px;
  padding: 9px 20px;
  border-bottom: 1px solid #ededed;

  @media (min-width: 768px) {
    padding: 37px 37px 50px 40px;
    margin: 0;
    border: none;
  }

  @media (min-width: 1300px) {
    display: inline-block;
    vertical-align: middle;
    margin: 0 36px 0 0;
    padding: 37px 0 0 0;
    width: 570px;
  }
}

.worksheet__program-choise {
  display: flex;
  flex-direction: column;
  margin: 0 -20px;
  padding: 13px 20px;
  border-bottom: 1px solid #ededed;

  @media (min-width: 768px) {
    padding: 18px 28px;
    margin: 0 0 0 40px;
    width: 631px;
    border: 1px solid #ededed;
  }

  @media (min-width: 1300px) {
    display: inline-block;
    vertical-align: middle;
    margin: 15px 0 0 40px;
    width: 570px;
  }
}


.worksheet__choise-box {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
}

.worksheet__choise {
  &:focus + .worksheet__label-name {
    text-decoration: underline;
  }

  &:checked + .worksheet__label-name::before {
    background-color: @colorThemeGreen;
  }

  &:disabled + .worksheet__label-name,
  &:disabled + .worksheet__label-name::before {
    opacity: 0.3;
  }
}

.worksheet__label-name--choise {
  padding: 6px 0 1px 0;
  margin: 0;

  &::before {
    display: inline-block;
    vertical-align: middle;
    content: "";
    width: 30px;
    height: 30px;
    border: 1px solid #d1d1d1;
    border-radius: 50%;
    padding: 8px;
    margin: 0 16px 6px 0;

    background-color: transparent;
    background-clip: content-box;

    @media (min-width: 768px) {
      margin: 0 18px 6px 0;
      padding: 10px;
      width: 40px;
      height: 40px;
    }
  }

  &:focus {
    outline: none;
  }

  &:focus::before,
  &:hover::before {
    border-color: #bfbfbf;
  }

  &:active,
  &:active::before {
    opacity: 0.3;
  }
}

.worksheet__box-title--advanced {
  @media (min-width: 768px) {
    margin: -25px 0 58px 0;
  }

  @media (min-width: 1300px) {
    margin: -25px 0 58px -40px;
  }
}

.worksheet__master-info {
  margin: 0 -20px;
  padding: 42px 0 17px 0;

  @media (min-width: 768px) {
    margin: 0;
    padding: 98px 0 23px 0;
  }

  @media (min-width: 1300px) {
    padding: 72px 0 23px 0;
  }
}

.worksheet__comment {
  margin: 0 -20px;
  padding: 13px 0 25px 0;

  @media (min-width: 768px) {
    margin: 0;
    padding: 44px 0 25px 0;
  }

  @media (min-width: 1300px) {
    padding: 47px 0 25px 0;
  }
}

.worksheet__comment-field {
  padding: 13px;
  width: 100%;
  height: 150px;
  border: 1px solid #d1d1d1;

  font-size: 16px;
  line-height: 18px;
  font-family: "Oswald", "Arial", sans-serif;
  text-transform: uppercase;

  background: none;
  resize: vertical;

  &:focus {
    outline-color: #000000;
  }

  &:hover {
    border-color: #cdcdcd;
  }

  @media (min-width: 768px) {
    padding: 13px 18px;
    height: 174px;

    font-size: 20px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    width: 100%;
  }
}

.worksheet__advanced-stuff {
  padding: 15px 0 0 0;
  margin: 0 -20px 33px -20px;

  @media (min-width: 768px) {
    padding: 59px 0 20px 0;
    margin: 0;
  }

  @media (min-width: 1300px) {
    padding: 62px 0 17px 0;
  }
}

.worksheet__box--advanced {
  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 39px;
  }

  @media (min-width: 1300px) {
    padding: 0;
  }
}

.worksheet__advanced-box {
  display: flex;
  flex-wrap: wrap;
  margin: 0 0 10px 0;

  @media (min-width: 768px) {
    flex: 286px 0 0;
    max-width: 286px;
    margin: 0 0 33px 0;
  }

  @media (min-width: 1300px) {
    flex: 246px 0 0;
    max-width: 246px;
    justify-content: space-between;
    margin: 0 0 42px 0;
  }
}

.worksheet__label-name--advanced {
  margin: 0;
  padding: 2px 0 0 0;

  &::before {
    display: inline-block;
    vertical-align: middle;
    content: "";
    width: 30px;
    height: 30px;
    border: 1px solid #cdcdcd;
    margin: 0 15px 0 0;

    font-size: 24px;
    line-height: 1;
    color: @colorThemeGreen;
    font-family: "Oswald", "Arial", sans-serif;

    @media (min-width: 768px) {
      margin: -2px 20px 0 0;
      width: 40px;
      height: 40px;
    }
  }

  &:focus {
    outline: none;
  }

  &:focus::before,
  &:hover::before {
    border-color: #9c9b9b;
  }

  &:active,
  &:active::before {
    opacity: 0.3;
  }
}

.worksheet__checkbox {
  &:checked + .worksheet__label-name--advanced::before {
    background-image: url("../img/icon-tick.svg");
    background-repeat: no-repeat;
    background-position: center;
    background-size: 21px;
  }

  &:focus + .worksheet__label-name--advanced::before {
    border-color: #9c9b9b;
  }

  &:disabled + .worksheet__label-name--advanced,
  &:disabled + .worksheet__label-name--advanced::before {
    opacity: 0.3;
  }
}

.worksheet__submit {
  width: 100%;
  border: none;
  margin: 0 0 18px 0;

  @media (min-width: 768px) {
    display: inline-flex;
    vertical-align: middle;
    padding: 16px;
    margin: 0 0 18px 39px;
    width: 284px;

    font-size: 20px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    width: 570px;
    margin: 0;
  }
}

.worksheet__explanation {
  display: block;
  text-align: center;

  font-family: "Arial", sans-serif;
  line-height: 18px;

  @media (min-width: 768px) {
    display: inline-block;
    vertical-align: middle;
    margin: -22px 0 0 58px;

    font-size: 16px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    margin: -2px 0 0 78px;
  }
}
