.page-header {
  font-family: "Oswald", "Arial", sans-serif;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: 768px) {
    border: none;
  }
}

.page-header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  padding: 0;

  @media (min-width: 768px) {
    padding: 18px 0;
  }

  @media (min-width: 1300px) {
    padding: 50px 0;
  }
}

.page-header__logo-link {
  margin-left: 6%;
  padding: 13px 0 12px 0;

  &:focus {
    outline: none;
  }

  &[href]:hover,
  &[href]:focus {
    opacity: 0.8;
  }

  &[href]:active {
    opacity: 0.6;
  }

  @media (min-width: 768px) {
    padding: 0;
    margin: 5px 0;
    width: 173px;
  }

  @media (min-width: 1300px) {
    margin: 2px 0;
  }
}

.page-header__pic-logo {
  @media (min-width: 768px) {
    width: 173px;
    height: 58px;
  }

  @media (min-width: 1300px) {
    width: 202px;
    height: 59px;
  }
}

.page-header__menu {
  flex: 100% 0 0;
  min-width: 320px;
  border-top: 1px solid #e6e6e6;

  text-transform: uppercase;

  @media (min-width: 768px) {
    flex: 440px 0 0;
    max-width: 440px;
    margin-left: auto;
    border: none;
  }

  @media (min-width: 1300px) {
    flex: 480px 0 0;
    max-width: 480px;
  }
}

.page-header__menu--index {
  @media (min-width: 1300px) {
    position: relative;
    z-index: 10;
    height: 72px;
  }
}

.page-header__menu-btn {
  margin: 0 6% 0 auto;
  padding: 0;
  width: 24px;
  height: 18px;
  border: none;

  background-image: url("../img/icon-mobile-menu.svg");
  background-repeat: no-repeat;
  background-color: transparent;
  background-position: center;

  &:focus {
    outline: none;
  }

  &:hover,
  &:focus {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: 768px) {
    display: none;
  }
}

.page-header__menu-btn--close {
  background-image: url("../img/icon-close.svg");
}

.page-header__menu-btn--nojs {
  display: none;
}

.page-header__list {
  .list-reset;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.page-header__list-item {
  display: flex;
  justify-content: center;
  padding: 21px 0 12px 0;
  border-bottom: 1px solid #e6e6e6;

  &:last-child {
    border: none;
  }

  @media (min-width: 768px) {
    padding: 24px 0 12px 0;
    border: none;
  }
}

.page-header__link {
  padding-bottom: 10px;
  border-bottom: 2px solid transparent;

  font-size: 20px;
  line-height: 1;
  font-weight: 500;

  &:focus {
    outline: none;
  }

  &[href]:focus,
  &[href]:hover {
    color: #707070;
  }

  &[href]:active {
    color: #b7b7b7;
  }

  @media (min-width: 768px) {
    font-size: 18px;
    line-height: 24px;
    font-weight: 400;
  }

  @media (min-width: 1300px) {
    font-size: 20px;
  }
}

.page-header__link--index {
  &[href]:hover,
  &[href]:focus {
    @media (min-width: 1300px) {
      color: #c1ddb1;
    }
  }

  &[href]:active {
    @media (min-width: 1300px) {
      color: #93c276;
    }
  }

  @media (min-width: 1300px) {
    color: #ffffff;
  }
}

.page-header__link--current {
  border-color: @colorThemeGreen;
}

.page-header__link--current-index {
  @media (min-width: 1300px) {
    border-color: #ffffff;
  }
}

.page-header__menu--hide {
  @media (max-width: 767px) {
    display: none;
  }
}
