.service-link {
  display: flex;
  justify-content: center;
  padding: 11px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-decoration: none;
  color: #ffffff;
  text-transform: uppercase;

  background-color: @colorThemeGreen;

  &:focus {
    outline: none;
  }

  &:hover,
  &:focus {
    background-color: #5eaa2f;
  }

  &:active {
    color: #8fc46e;
  }
}

.service-link--catalog {
  color: #444444;

  background-color: #f2f2f2;

  &:focus,
  &:hover {
    background-color: #ebebeb;
  }

  &:active {
    color: #b9b9b9;
  }
}
