.advanced-products {
  padding: 24px 0 35px 0;

  @media (min-width: 768px) {
    padding: 34px 0 74px 0;
  }

  @media (min-width: 1300px) {
    padding: 29px 0 88px 0;
  }
}

.advanced-products__title {
  display: inline-block;
  vertical-align: middle;
  margin: -17px 0 34px -2px;
  padding: 0 20px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 1;
  font-weight: normal;
  color: #111111;

  background-color: #ffffff;

  @media (min-width: 768px) {
    padding: 0 40px;
    margin: -25px 0 77px -2px;

    font-size: 36px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    margin: -24px 0 80px -40px;
  }
}

.advanced-products__box {
  padding: 0 20px;

  @media (min-width: 1300px) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.advanced-products__list {
  .list-reset;

  @media (min-width: 768px) {
    margin: 0 -20px;
  }

  @media (min-width: 1300px) {
    width: 897px;
  }
}

.advanced-products__list-item {
  padding: 14px 0 15px 0;

  border-top: 1px solid #cdcdcd;

  &:nth-child(n+2) {
    @media (min-width: 768px) {
      border-top: none;
    }
  }

  &:last-child {
    border-bottom: 1px solid #cdcdcd;
  }

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    padding: 26px 36px 26px 38px;
    border: 1px solid #cdcdcd;
  }

  @media (min-width: 1300px) {
    padding: 25px 0 14px 0;
    border-right: none;
    border-left: none;
  }
}

.advanced-products__item-title {
  margin: 0 0 9px 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1;
  font-weight: normal;
  text-transform: uppercase;
  color: #222222;

  @media (min-width: 768px) {
    flex-grow: 2;
    margin: 0;
    font-size: 20px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    flex: 325px 0 0;
  }
}

.advanced-products__item-value {
  font-size: 14px;
  line-height: 1;
  font-weight: normal;

  font-family: "Arial", sans-serif;
  text-transform: none;

  &:first-of-type {
    @media (min-width: 1300px) {
      flex: 245px 0 0;
    }
  }

  &:nth-child(odd) {
    padding: 2px 0 0 0;
    float: right;

    @media (min-width: 768px) {
      margin: 0 0 0 52px;
      padding: 5px 0 0 0;
      float: none;
    }

    @media (min-width: 1300px) {
      margin: 0;
      padding: 5px 0 0 0;
    }
  }

  @media (min-width: 768px) {
    padding: 5px 0 0 0;
    margin: 0;

    font-size: 16px;
    line-height: 20px;
  }
}

.advanced-products__link {
  margin: 14px 0 0 0;

  @media (min-width: 768px) {
    margin: 20px 0 0 0;
    flex: 632px 0 0;
    max-width: 632px;
  }

  @media (min-width: 1300px) {
    margin: -10px 0 0 auto;
    flex: 245px 0 0;
    max-width: 245px;
  }
}

.advanced-products__gift {
  padding: 65px 0 40px 0;
  margin: 40px 0 0 0;

  font-size: 16px;
  line-height: 20px;
  font-family: "Arial", sans-serif;
  text-transform: none;
  color: #ffffff;

  background-color: #5fa336;
  background-image: url("../img/gift-photo-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/gift-photo-mobile@2x.jpg");
    background-size: 280px auto;
  }

  @media (min-width: 768px) {
    position: relative;
    margin: 69px -20px 0 -20px;
    padding: 72px 120px 68px 120px;

    background-color: #5fa336;
    background-image: url("../img/gift-photo-tablet@1x.jpg");
    background-repeat: no-repeat;
    background-position: center;
  }

  @media (min-width: 768px) and (min-resolution: 144dpi), (min-width: 768px) and (min-resolution: 1.5dppx) {
    background-image: url("../img/gift-photo-tablet@2x.jpg");
    background-size: 708px auto;
  }

  @media (min-width: 1300px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    flex: 245px 0 0;
    max-width: 245px;
    margin: 0 -20px;
    padding: 65px 0 40px 0;

    background-color: #5fa336;
    background-image: url("../img/gift-photo-desktop@1x.jpg");
    background-repeat: no-repeat;
    background-position: center;
  }

  @media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
    background-image: url("../img/gift-photo-desktop@2x.jpg");
    background-size: 245px auto;
  }
}

.advanced-products__gift-description {
  display: block;
  margin: 0 auto;
  width: 165px;

  text-align: center;

  &::before {
    display: block;
    content: "";
    width: 55px;
    height: 60px;
    margin: 0 auto 62px auto;

    background-image: url("../img/icon-gift.svg");
    background-repeat: no-repeat;

    @media (min-width: 768px) {
      position: absolute;
      left: 91px;
      top: 65px;
    }

    @media (min-width: 1300px) {
      position: static;
    }
  }

  @media (min-width: 768px) {
    margin: 0 0 0 auto;

    text-align: left;
  }

  @media (min-width: 1300px) {
    margin: 0 auto;

    text-align: center;
  }
}
