.presentation {
  padding: 28px 0 22px 0;

  color: #ffffff;
  font-family: "Oswald", "Arial", sans-serif;

  background-color: #ffffff;
  background-image: url("../img/index-background-mobile@1x.jpg"), linear-gradient(#65a93c 305px, #ffffff 1px);
  background-repeat: no-repeat;
  background-position: top center;

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/index-background-mobile@2x.jpg"), linear-gradient(#65a93c 305px, #ffffff 1px);
    background-size: 320px auto, auto;
  }

  @media (min-width: 768px) {
    padding: 33px 0 531px 0;
    margin: 0;

    background-color: #ffffff;
    background-image: none;
  }

  @media (min-width: 1300px) {
    margin: -171px 0 80px 0;
    padding: 222px 0 189px 0;

    background-image: url("../img/index-background-desktop@1x.jpg"), linear-gradient(to right, #ffffff 50%, #64a83b 50%);
    background-repeat: no-repeat;
    background-position: top right;
  }

  @media (min-width: 1300px) and (min-resolution: 144dpi), (min-width: 1300px) and (min-resolution: 1.5dppx) {
    background-image: url("../img/index-background-desktop@2x.jpg"), linear-gradient(to right, #ffffff 50%, #64a83b 50%);
    background-size: 720px auto, auto;
  }
}

.presentation__wrapper {
  padding: 0 20px;

  @media (min-width: 768px) {
    position: relative;
    display: flex;
    flex-direction: column;
    padding: 0 0 0 86px;
  }

  @media (min-width: 1300px) {
    padding: 0 0 0 78px;
  }
}

.presentation__title {
  margin: 0 auto;

  font-size: 36px;
  line-height: 1;
  font-weight: normal;

  text-transform: none;
  text-align: center;

  @media (min-width: 768px) {
    margin: 0;
    width: 420px;

    color: #000000;
    font-size: 60px;
    line-height: 1;
    font-weight: 400;
    text-align: left;
  }
}

.presentation__slogan {
  display: block;
  margin-top: 23px;

  font-size: 14px;
  line-height: 1;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;

  @media (min-width: 768px) {
    margin: 43px 0 46px 0;
    width: 290px;

    color: #000000;
    font-size: 20px;
    line-height: 1;
    font-weight: normal;
  }

  @media (min-width: 1300px) {
    margin: 41px 0 52px 0;
  }
}

.presentation__pic {
  display: block;
  margin: 30px auto 0;

  @media (min-width: 768px) {
    position: absolute;
    top: 171px;
    right: 0;
  }

  @media (min-width: 1300px) {
    top: -109px;
    right: 217px;
  }
}

.presentation__choose-program {
  margin: 0 auto;
  font-size: 14px;
  line-height: 18px;
  font-weight: normal;

  @media (min-width: 768px) {
    position: relative;
    z-index: 2;
    margin: 0;
    padding: 16px;
    width: 248px;

    font-size: 20px;
    line-height: 1;
  }
}
