.catalog {
  padding: 0 0 18px 0;

  @media (min-width: 768px) {
    padding: 0 0 55px 0;
  }

  @media (min-width: 1300px) {
    padding: 0 0 66px 0;
  }
}

.catalog__title {
  margin: 0;
  padding: 15px 20px 27px 20px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
  color: #111111;

  @media (min-width: 768px) {
    margin: 0;
    padding: 32px 0 25px 0;

    font-size: 60px;
  }

  @media (min-width: 1300px) {
    margin: 0 0 0 0;
    padding: 20px 0;
  }
}

.catalog__item-title {
  width: 140px;
  margin: 0;
  padding: 20px 65px 10px 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
  text-transform: uppercase;
  color: #111111;

  @media (min-width: 768px) {
    margin: 0 0 19px 0;
    width: 130px;
    padding: 0;

    font-size: 20px;
    line-height: 1;
    text-align: center;
  }

  @media (min-width: 1300px) {
    margin: 0 0 21px 0;
  }
}

.catalog__list {
  .list-reset;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
  }
}

.catalog__list-item {
  position: relative;
  margin: 0;
  padding: 1px 20px 21px 20px;
  border-top: 1px solid #e6e6e6;

  &:nth-child(odd) {
    @media (min-width: 768px) {
      margin-right: 60px;
    }

    @media (min-width: 1300px) {
      margin-right: 80px;
    }
  }

  &:nth-child(4n) {
    @media (min-width: 1300px) {
      margin-right: 0;
    }
  }

  @media (min-width: 768px) {
    margin: 140px 0 0 0;
    padding: 163px 20px 40px 20px;
    width: 324px;
    border: none;

    background-color: #f2f2f2;
  }

  @media (min-width: 1300px) {
    margin: 142px 80px 0 0;
    width: 245px;
    padding: 163px 0 39px 0;
  }
}

.catalog__list-item--show-more {
  padding: 27px 20px 23px 20px;
  border-bottom: 1px solid #e6e6e6;

  @media (min-width: 768px) {
    padding: 55px 20px 40px 20px;
    border: 1px solid #e6e6e6;
    background-color: #ffffff;
  }
}

.catalog__item-box {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  flex-wrap: wrap;
  width: 280px;
  margin: 0 auto;

  @media (min-width: 768px) {
    position: static;
    margin: 0 0 31px 0;
    width: auto;
    align-items: center;
  }

  @media (min-width: 1300px) {
    width: 172px;
    margin: 0 auto 31px auto;
  }
}

.catalog__char-table {
  width: 140px;
  height: 54px;
  margin: 0 0 20px 0;
  border-collapse: collapse;

  font-size: 12px;
  line-height: 16px;
  font-weight: normal;

  @media (min-width: 768px) {
    margin: 0;
    width: 255px;

    font-size: 16px;
  }

  @media (min-width: 1300px) {
    width: 100%;
  }
}

.catalog__show-more {
  margin: 0 0 7px 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;
  color: #111111;

  &::before {
    display: block;
    content: "";
    width: 30px;
    height: 30px;
    margin: 0 auto 23px auto;

    background-image: url("../img/icon-plus-mobile.svg");
    background-repeat: no-repeat;

    @media (min-width: 768px) {
      margin: 0 auto 49px auto;
      width: 60px;
      height: 60px;

      background-image: url("../img/icon-plus.svg");
      background-repeat: no-repeat;
    }
  }

  @media (min-width: 768px) {
    width: 145px;
    margin: 0 auto 28px auto;

    font-size: 20px;
  }
}

.catalog__reclame {
  display: block;
  margin: 0 0 21px 0;
  align-items: center;
  font-size: 12px;
  line-height: 16px;
  font-weight: normal;
  font-family: "Arial", sans-serif;
  text-transform: none;
  text-align: center;

  @media (min-width: 768px) {
    width: 170px;
    margin: 0 auto 86px auto;

    font-size: 16px;
  }
}

.catalog__link {
  width: 100%;
  align-self: center;

  @media (min-width: 768px) {
    width: 249px;
    margin: 0 auto;
  }

  @media (min-width: 1300px) {
    width: 168px;
    margin: 0 auto;
  }
}

.catalog__char-row {
  display: flex;
  justify-content: space-between;

  @media (min-width: 768px) {
    padding: 6px 0 7px 2px;
    border-bottom: 1px solid #cdcdcd;
  }
}

.catalog__pic {
  position: absolute;
  left: 0;
  top: 0;

  @media (min-width: 768px) {
    top: -44px;
  }
}

.catalog__pic--big {
  @media (min-width: 768px) {
    top: -81px;
  }
}
