.advantages {
  padding: 0 20px 23px 20px;

  @media (min-width: 768px) {
    padding: 0 0 82px 0;
    border-bottom: 1px solid #e6e6e6;
  }

  @media (min-width: 1300px) {
    padding: 45px 0 66px 0;
    border: none;
  }
}

.advantages__wrapper {
  @media (min-width: 768px) {
    padding: 0;
  }
}

.advantages__title {
  margin: 0 0 25px 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
  color: #111111;

  @media (min-width: 768px) {
    margin: 0 0 55px 0;
    padding: 16px 0;

    font-size: 60px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    margin: 0 0 70px 0;
    padding: 0;
  }
}

.advantages__list {
  .list-reset;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.advantages__list-item {
  &:nth-child(n+3) {
    @media (min-width: 768px) {
      margin: 115px 0 0 0;
    }

    @media (min-width: 1300px) {
      margin: 0;
    }
  }

  &:last-child {
    @media (min-width: 1300px) {
      margin-right: 0;
    }
  }

  @media (min-width: 768px) {
    flex: 325px 0 0;
    max-width: 325px;
  }

  @media (min-width: 1300px) {
    flex: 245px 0 0;
    max-width: 245px;
    margin-bottom: 40px;
  }
}

.advantages__description {
  position: relative;
  display: flex;
  align-items: center;
  margin: 0 0 20px 0;

  font-family: "Arial", sans-serif;
  text-transform: none;

  &::before {
    content: "";
    display: block;
    margin: 0 20px 0 0;
    flex: 60px 0 0;
    max-width: 60px;
    height: 60px;

    background-color: @colorThemeGreen;
    background-position: center;
    background-repeat: no-repeat;

    @media (min-width: 768px) {
      width: 80px;
      max-width: 80px;
      height: 80px;
      margin: 0 0 31px 0;
    }
  }

  &::after {
    @media (min-width: 768px) {
      position: absolute;
      z-index: -1;
      content: "";
      font-size: 281px;
      font-family: "Oswald", "Arial", sans-serif;
      color: #f2f2f2;
    }
  }

  @media (min-width: 768px) {
    display: block;
    width: 250px;

    font-size: 16px;
    line-height: 24px;
  }
}

.advantages__description--leaf::before {
  background-image: url("../img/advantage-leaf@1x.png");

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/advantage-leaf@2x.png");
    background-size: 50px auto;
  }
}

.advantages__description--powder::before {
  background-image: url("../img/advantage-powder@1x.png");

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/advantage-powder@2x.png");
    background-size: 50px auto;
  }
}

.advantages__description--dining::before {
  background-image: url("../img/advantage-dining@1x.png");

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/advantage-dining@2x.png");
    background-size: 50px auto;
  }
}

.advantages__description--clock::before {
  background-image: url("../img/advantage-clock@1x.png");

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    background-image: url("../img/advantage-clock@2x.png");
    background-size: 50px auto;
  }
}

.advantages__description--leaf::after {
  @media (min-width: 768px) {
    content: "1";
    top: 85px;
    right: 19px;
  }
}

.advantages__description--powder::after {
  @media (min-width: 768px) {
    content: "2";
    top: 89px;
    right: 1px;
  }
}

.advantages__description--dining::after {
  @media (min-width: 768px) {
    content: "3";
    top: 89px;
    right: 1px;
  }
}

.advantages__description--clock::after {
  @media (min-width: 768px) {
    content: "4";
    top: 89px;
    right: 0;
  }
}
