.example {
  padding: 17px 0 29px 0;

  font-family: "Oswald", "Arial", sans-serif;

  background-color: #f2f2f2;

  @media (min-width: 768px) {
    padding: 43px 0 0 0;

    background-color: #ffffff;
  }

  @media (min-width: 1300px) {
    padding: 0;
  }
}

.example__background {
  &::before {
    @media (min-width: 1300px) {
      content: "";
      flex: 1 0 0;
    }
  }

  &::after {
    @media (min-width: 1300px) {
      content: "";
      flex: 1 0 0;

      background-color: #eaeaea;
    }
  }

  @media (min-width: 768px) {
    position: relative;
    z-index: 0;
    background-image: linear-gradient(to bottom, #ffffff 450px, #f2f2f2 300px);
  }

  @media (min-width: 1300px) {
    display: flex;
    background-image: none;
    background-color: #f2f2f2;
  }
}

.example__wrapper {
  @media (min-width: 1300px) {
    position: relative;
    padding: 71px 650px 77px 0;
  }
}

.example__title {
  margin: 0;
  padding: 0 0 0 20px;

  font-size: 36px;
  line-height: 60px;
  font-weight: normal;
  color: #111111;

  @media (min-width: 768px) {
    width: 708px;
    padding: 0;
    margin: 0 auto;

    font-size: 60px;
    line-height: 1;
  }

  @media (min-width: 1300px) {
    width: 1220px;
    margin: 0 auto 75px auto;
  }
}

.example__info {
  @media (min-width: 768px) {
    width: 708px;
  }

  @media (min-width: 1300px) {
    width: 570px;
  }
}

.example__description {
  margin: 23px 0 26px 0;
  padding: 0 0 0 20px;
  width: 90%;

  font-family: "Arial", sans-serif;

  @media (min-width: 768px) {
    margin: 64px 0 53px 0;
    padding: 0;
    width: 680px;

    font-size: 16px;
    line-height: 24px;
    font-weight: normal;
  }

  @media (min-width: 1300px) {
    flex: 438px 0 0;
    max-width: 438px;
    margin: 0 0 69px 0;
  }
}

.example__features {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 280px;
  margin: 0 auto;

  @media (min-width: 768px) {
    width: 708px;
  }

  @media (min-width: 1300px) {
    width: 365px;
    margin: 0;
  }
}

.example__feature {
  flex: 124px 0 0;
  max-width: 124px;

  @media (min-width: 768px) {
    flex: 162px 0 0;
    max-width: 162px;
    margin-right: 42px;
  }

  @media (min-width: 1300px) {
    margin: 0;
  }
}

.example__feature-days {
  display: block;
  border: 1px solid #cdcdcd;
  border-radius: 2px;
  padding: 12px 5px 17px 5px;

  font-size: 24px;
  line-height: 1;
  font-weight: normal;
  text-transform: uppercase;
  text-align: center;
  word-break: break-word;
  color: #111111;

  @media (min-width: 768px) {
    padding-top: 14px;

    font-size: 30px;
    line-height: 37px;
  }
}

.example__feature-value {
  display: block;
  width: 85px;
  margin: -12px auto 0 auto;
  padding: 2px 10px;

  font-family: "Arial", sans-serif;
  font-size: 12px;
  line-height: 12px;
  font-weight: normal;
  text-align: center;
  word-break: break-word;

  background-color: #f2f2f2;

  @media (min-width: 768px) {
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: -25px 0 0 22px;

    font-size: 14px;
    line-height: 1;

    background-color: #ffffff;
  }

  @media (min-width: 1300px) {
    background-color: #f2f2f2;
  }
}

.example__feature-value--time {
  @media (min-width: 768px) {
    margin: -25px 0 0 8px;
  }
}

.example__costs {
  flex: 100% 0 0;
  max-width: 100%;

  margin: 19px 0 44px 0;

  font-size: 14px;
  line-height: 20px;
  font-weight: normal;
  text-align: center;
  text-transform: uppercase;
  color: #111111;

  @media (min-width: 768px) {
    flex: 176px 0 0;
    max-width: 176px;
    margin: 10px 0 0 auto;

    text-align: right;
    font-size: 20px;
  }

  @media (min-width: 1300px) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    flex: 327px 0 0;
    max-width: 327px;
    margin: 41px 0 0 0;
  }
}

.example__costs-price {
  @media (min-width: 768px) {
    display: block;
    margin: 9px 0 0 0;
  }

  @media (min-width: 1300px) {
    margin: 0;
  }
}

.example__demo-container {
  position: relative;
  margin: 0 auto;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    padding: 74px 0 69px 0;
  }

  @media (min-width: 1300px) {
    position: absolute;
    z-index: 0;
    top: -190px;
    right: -39px;
    display: flex;
    flex-wrap: wrap;
    flex: 690px 0 0;
    max-width: 690px;
    padding: 0 0 77px 0;
    margin: 0;
  }
}

.example__img-container {
  position: relative;
  margin: -11px auto 0 auto;
  width: 320px;

  @media (min-width: 768px) {
    width: 690px;
    height: 520px;
    margin: 0 auto;
  }

  @media (min-width: 1300px) {
    margin: 0;
  }
}

.example__box-background {
  @media (min-width: 768px) {
    z-index: -2;
    position: absolute;
    height: 464px;
    width: 54.5%;
    bottom: 0;
    right: -30px;

    background-color: #eaeaea;
  }

  @media (min-width: 1300px) {
    width: 47.4%;
    height: 472px;
    right: 0;
  }
}

.example__btn {
  border-top: none;
  border-right: none;
  border-left: none;
  border-bottom: 1px dotted #000000;
  padding: 0 0 7px 0;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  text-transform: uppercase;

  background: none;

  &:focus {
    outline: none;
  }

  &:focus,
  &:hover {
    border-bottom-style: solid;
  }

  &:active {
    border-bottom-color: transparent;
  }

  @media (min-width: 768px) {
    font-size: 20px;
  }
}

.example__cat-pic {
  display: block;

  @media (min-width: 768px) {
    max-width: none;
    width: 690px;
    height: 517px;
  }
}

.example__cat-box {
  @media (min-width: 768px) {
    display: flex;

    overflow-x: hidden;
  }
}

.example__cat-box--before {
  position: relative;
  z-index: 1;

  background-color: #f2f2f2;

  @media (min-width: 768px) {
    position: absolute;
    z-index: -1;
    width: 49%;
    margin: -6px 0 0 8px;

    background-color: transparent;
  }

  @media (min-width: 1300px) {
    width: 52.9%;
    margin: 0;
  }
}

.example__cat-box--after {
  position: absolute;
  top: 0;
  z-index: 0;

  @media (min-width: 768px) {
    z-index: -1;
    flex-direction: row-reverse;
    margin: -6px;
    width: 50.7%;
    top: 0;
    right: 0;
  }

  @media (min-width: 1300px) {
    margin: 0;
    width: 47.1%;
  }
}

.example__control {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 200px;
  margin: 31px auto 0 auto;

  @media (min-width: 768px) {
    align-items: center;
    width: 574px;
    margin: 9px auto 0 auto;
  }

  @media (min-width: 1300px) {
    width: 572px;
    margin: 37px auto 0 78px;
  }
}

.example__before-after {
  position: relative;
  flex: 84px 0 0;
  max-width: 84px;
  height: 14px;
  border: 1px solid #cdcdcd;
  border-radius: 5px;
  margin: 3px 0 0 3px;

  background-color: #ffffff;

  @media (min-width: 768px) {
    flex: 428px 0 0;
    max-width: 428px;
    height: 6px;
    border: none;
    margin: -3px 0 0 3px;

    background-color: #dcdcdc;
  }
}

.example__btn-scale {
  position: absolute;
  width: 35px;
  top: 50%;
  left: 7%;
  right: 50%;
  height: 6px;
  border: none;
  border-radius: 5px;

  background-color: #68b738;
  transform: translate(0, -50%);

  &:focus {
    outline: none;
  }

  &:focus,
  &:hover {
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.3);
  }

  &:active {
    border-color: #68b738;
  }

  &::before {
    @media (min-width: 768px) {
      content: "";
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 8px;
      height: 8px;
      background-color: #68b738;
      border-radius: 50%;
    }
  }

  @media (min-width: 768px) {
    top: 50%;
    left: 50%;
    width: 34px;
    height: 34px;
    padding: 0;
    border: 2px solid #cdcdcd;
    border-radius: 50%;

    background-color: #ffffff;
    box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.15);

    cursor: pointer;
    transform: translate(-50%, -50%);
  }
}

// Модификатор для переключения "было-стало" в мобильной версии. Присваивается cat-box--before

.example__cat-box--toggle {
  visibility: hidden;
}
