.page-footer {
  background-color: #f2f2f2;

  @media (min-width: 1300px) {
    position: relative;
  }
}

.page-footer__cooperation {
  background-color: #f2f2f2;

  @media (min-width: 1300px) {
    position: absolute;
    width: 100%;
    height: 400px;

    background-color: transparent;
  }
}

.page-footer__cooperation--main {
  background-color: #ffffff;

  @media (min-width: 1300px) {
    background-color: transparent;
  }
}

.page-footer__container {
  @media (min-width: 768px) {
    width: 708px;
    margin: 0 auto;
  }

  @media (min-width: 1300px) {
    width: 1220px;
  }
}

.page-footer__cooperation-box {
  display: flex;
  justify-content: space-around;
  padding: 28px 20px 26px 19px;

  @media (min-width: 768px) {
    justify-content: space-between;
    padding: 64px 41px 57px 41px;
  }

  @media (min-width: 1300px) {
    position: relative;
    z-index: 10;
    padding: 64px 85px 60px 75px;
    top: 95px;
    left: 1px;
    width: 570px;

    background-color: #ffffff;
  }
}

.page-footer__cooperation-title {
  margin: 0 14px 0 0;
  width: 125px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: normal;
  text-transform: uppercase;
  color: #111111;

  @media (min-width: 768px) {
    flex: 160px 0 0;
    max-width: 160px;

    font-size: 20px;
    line-height: 25px;
  }
}

.page-footer__cooperation-adress {
  margin: 0;
  width: 140px;

  font-size: 14px;
  line-height: 20px;
  font-weight: normal;

  @media (min-width: 768px) {
    margin: 0 0 0 auto;
    flex: 170px 0 0;
    max-width: 170px;

    font-size: 16px;
    line-height: 20px;
    text-align: right;
  }

  @media (min-width: 1300px) {
    flex: 160px 0 0;
    max-width: 160px;
    margin: 0;

    text-align: left;
  }
}

.page-footer__cooperation-sp {
  display: inline-block;
  vertical-align: middle;

  @media (min-width: 768px) {
    margin-top: 19px;
  }
}

.page-footer__map {
  height: 363px;

  @media (min-width: 768px) {
    height: 400px;

    background-image: url("../img/bg-map-tablet@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }

  @media (min-width: 1300px) {
    background-image: url("../img/bg-map-desktop@1x.png");
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
  }
}

.page-footer__google-map {
  position: relative;
  z-index: 1;
  width: 100%;
  height: 100%;
}

.page-footer__wrapper {
  padding: 37px 20px 7px 20px;

  @media (min-width: 768px) {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    padding: 41px 0 45px 0;
  }
}

.page-footer__list {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
  padding: 18px 63px 17px 59px;
  margin: 0;
  border-bottom: 1px solid #cdcdcd;
  border-top: 1px solid #cdcdcd;

  list-style: none;

  @media (min-width: 768px) {
    justify-content: space-between;
    flex: 166px 0 0;
    max-width: 166px;
    border: none;
    margin: 0 auto;
    padding: 0 5px 0 24px;
  }
}

.page-footer__list-item {
  flex: 35px 0 0;
  max-width: 35px;
}

.page-footer__link {
  display: block;
  padding: 5px;

  &:focus {
    outline: none;
  }

  &:focus .page-footer__icon,
  &:hover .page-footer__icon {
    fill: @colorThemeGreen;
  }

  &:active .page-footer__icon {
    opacity: 0.3;
  }

  @media (min-width: 768px) {
    width: 100%;
    padding: 10px 0;
  }
}

.page-footer__logo-link {
  display: block;
  margin: 0 auto 27px auto;
  width: 130px;

  &[href]:focus {
    outline: none;
  }

  &[href]:focus,
  &[href]:hover {
    opacity: 0.8;
  }

  &[href]:active {
    opacity: 0.6;
  }

  @media (min-width: 768px) {
    flex: 132px 0 0;
    max-width: 132px;

    padding: 0;
    margin: 5px 0 0 0;
  }
}

.page-footer__for-academy {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 8px 0;
  width: 280px;
  margin: 0 auto;

  &:focus {
    outline: none;
  }

  &:focus .page-footer__academy,
  &:hover .page-footer__academy {
    opacity: 0.8;
  }

  &:focus .page-footer__acad,
  &:hover .page-footer__acad {
    fill: @colorThemeGreen;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: 768px) {
    flex: 162px 0 0;
    max-width: 162px;
    margin: 0;
    padding: 4px 0;
  }
}

.page-footer__academy {
  padding: 10px 0;
  margin-left: -2px;

  font-size: 16px;
  line-height: 1;
  font-weight: normal;
  font-family: "Arial", sans-serif;
  text-transform: none;
  color: #444444;
}

.page-footer__acad {
  margin: 7px -7px 0 0;
  width: 42px;
  height: 49px;
}

.page-footer__icon {
  display: block;
  margin: 0 auto;
}

.page-footer__icon--vk {
  @media (min-width: 768px) {
    width: 30px;
    height: 16px;
  }
}

.page-footer__icon--insta {
  @media (min-width: 768px) {
    width: 23px;
    height: 23px;
    margin: 0 0 0 auto;
  }
}

.page-footer__icon--fb {
  @media (min-width: 768px) {
    width: 14px;
    height: 28px;
  }
}
