.programs {
  padding: 0 20px;

  @media (min-width: 768px) {
    padding: 0;
  }
}

.programs__list {
  .list-reset;

  @media (min-width: 1300px) {
    display: flex;
    justify-content: space-between;
  }
}

.programs__title {
  position: relative;
  margin: 0;
  padding: 9px 30px 0 71px;

  font-family: "Oswald", "Arial", sans-serif;
  font-size: 24px;
  line-height: 37px;
  font-weight: normal;
  text-transform: uppercase;
  word-break: break-all;
  color: #111111;

  &::before {
    position: absolute;

    content: "";
    z-index: 1;
  }

  &::after {
    position: absolute;
    top: 5px;
    left: 2px;
    width: 50px;
    height: 51px;

    content: "";
    z-index: 0;

    background-color: @colorThemeGreen;
    border-radius: 50%;

    @media (min-width: 768px) {
      top: 92px;
      left: auto;
      right: 60px;
      width: 200px;
      height: 200px;
    }

    @media (min-width: 1300px) {
      top: 48px;
      left: 50px;
      width: 100px;
      height: 100px;
    }
  }

  @media (min-width: 768px) {
    position: static;
    width: 100%;
    padding: 0;
    margin: 0;

    font-size: 36px;
    line-height: 37px;
  }

  @media (min-width: 1300px) {
    padding: 0 95px 0 164px;
  }
}

.programs__title--slim::before {
  position: absolute;
  top: 2px;
  left: 10px;
  width: 35px;
  height: 49px;

  background-image: url("../img/icon-cat-slim.svg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: 768px) {
    top: 80px;
    left: auto;
    right: 92px;
    width: 140px;
    height: 194px;
  }

  @media (min-width: 1300px) {
    top: 40px;
    left: 65px;
    width: 70px;
    height: 97px;
  }
}

.programs__title--pro::before {
  position: absolute;
  top: 16px;
  left: -7px;
  width: 67px;
  height: 28px;

  background-image: url("../img/icon-cat-pro.svg");
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: 768px) {
    top: 136px;
    left: auto;
    right: 28px;
    width: 268px;
    height: 112px;
  }

  @media (min-width: 1300px) {
    top: 70px;
    left: 33px;
    width: 134px;
    height: 56px;
  }
}

.programs__description {
  width: 100%;
  margin: 0 0 20px 0;
  padding: 30px 0 20px 0;
  border-bottom: 1px solid #cecece;

  @media (min-width: 768px) {
    padding: 25px 0 55px 0;
    margin: 0;
    width: 380px;
    border: none;

    font-size: 16px;
    line-height: 24px;
  }

  @media (min-width: 1300px) {
    width: 100%;
    margin: 75px 0 30px 0;
    padding: 0;
  }
}

.programs__description--pro {
  @media (min-width: 768px) {
    width: 340px;
  }

  @media (min-width: 1300px) {
    width: 100%;
  }
}

.programs__list-item {
  margin-bottom: 19px;
  padding: 22px 7.2% 19px 7.2%;

  background-color: #f2f2f2;

  @media (min-width: 768px) {
    position: relative;
    margin: 0 0 30px 0;
    padding: 75px 52px 60px 52px;
  }

  @media (min-width: 1300px) {
    flex: 570px 0 0;
    max-width: 570px;
  }
}

.programs__arrow {
  display: inline-block;
  vertical-align: middle;
  margin: 0 0 6px 7px;

  @media (min-width: 768px) {
    margin: 0 0 6px 20px;
  }
}

.programs__arrow--long {
  display: none;
}

.programs__link {
  font-family: "Oswald", "Arial", sans-serif;
  font-size: 16px;
  line-height: 1;
  text-transform: uppercase;

  &:focus {
    outline: none;
  }

  &:focus .programs__arrow--long,
  &:hover .programs__arrow--long {
    display: inline-block;
  }

  &:focus .programs__arrow--short,
  &:hover .programs__arrow--short {
    display: none;
  }

  &:active {
    color: #b7b7b7;
  }

  &:active .programs__arrow--long {
    fill: #b7b7b7;
  }

  @media (min-width: 768px) {
    font-size: 20px;
    line-height: 1;
  }
}
